.footer{
  text-align: center;
  background: #F7F7F7;
  padding: 18px 0;

  .footer-logo{
    font-weight: 400;
    font-size: 22px;
  }

  .footer-holder{
    display: flex;
    align-items: center;
    justify-content: space-between;

    .menu li a {
      text-transform: none;
      font-size: 14px;
      line-height: 18px;
    }
  }

  .inst-fb {
    li {
      margin: 0;
    }
  }

  .copyright {
    padding: 16px 0 0 0;
    font-size: 14px;
    line-height: 18px;
    color: #8D8D8D;

  }
}